.body {
  width: 320px;
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  font-family: $main-font-1;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  background-color: $main-color-1;
  color: $text-color-1;

  @media (min-width: $tablet-width) {
    width: 769px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
  }
}
